
#result{padding:.6rem .8rem;width:auto;height:65%;background-color:#fff;border-radius:5px;text-align:left;overflow:auto;opacity:0;transition:300ms
}
#result.true{opacity:1;transition:300ms
}
#result ul{margin:0 0;padding:0 0
}
#result ul li{list-style:none
}
#result p.back{text-align:right
}
#result p.noDataInfo a{font-weight:bold;color:#1f8dd6;text-decoration:none
}
@media screen and (min-width:480px){
#result{padding:1rem 2rem
}
}


